@use 'global' as *;

.mv {
    padding-top: 100px;
    background-color: rgb(28, 18, 223);
    @include mq('md') {
       padding-top: 50px; 
    }
}

.mv__content {
    text-align: center;
    padding: rem(80);
}

.mv__title {
    margin-top: rem(32);
    color: $white;
    font-size: rem(56);
    font-family: $base-font-family;
}

.mv__message {
    margin-top: rem(32);
    color: $white;
    font-size: rem(24);
}